/* eslint-disable */
export default {
  data() {
    return {
      activeIndex: 1,
      activeName: 'one',
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      // 级联选择器假数据
      options: [
        {
          value: "zhinan",
          label: "指南",
          children: [
            {
              value: "shejiyuanze",
              label: "设计原则",
              children: [
                {
                  value: "shejiyuanze1",
                  label: "设计原则1",
                }
              ]
            },
            {
              value: "daohang",
              label: "导航"
            }
          ]
        }
      ],
      // 级联选择器配置对象
      defaultsProps: {
        // childre负责显示结构
        // label负责显示
        // value负责搜集id
        value: "cat_id",
        label: "cat_name"
      },
      addCatForm: {
        cat_name: [], //分类名
        cat_pid_arr: "" //父级id
      },
      // 添加商品对象
      addGoodsForm: {
        "goods_name": "",
        "goods_price": '',
        "goods_number": '',
        "goods_weight": '',
        "goods_cat": [],
        "goods_introduce": '',
        "pics": [],
        "radio": 1
      },
      dialogImageUrl: '',
      dialogVisible: false,
      headers: {
        Authorization: localStorage.getItem('token')
      },
      // 富文本编辑器的设置
      editorOption: {
        placeholder: '请输入文本'
      }
    };

  },
  created() {
    this.getCategories()
  },
  methods: {
    clickTab(e) {
      // console.log('点击了'+e.index);
      this.activeIndex = +e.index + 1
    },
    next(index, name) {
      this.activeIndex = index;
      this.activeName = name;
    },

    // 级联选择器获取分类数据
    async getCategories() {
      let res = await this.$axios.get("categories", {
        params: {
          type: 3
        }
      });
      // console.log(res);
      this.options = res.data.data;
    },
    // 上传图片
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    // 上传图片
    uploadImgSuccess(res) {
      // console.log(response);
      this.addGoodsForm.pics.push({ pic: res.data.tmp_path })
    },
    // 提交商品
    async postGoods() {
      const {
        goods_name,
        goods_price,
        goods_number,
        goods_weight,
        goods_cat,
        goods_introduce,
        pics,
        radio } = this.addGoodsForm;
      let res = await this.$axios.post("goods",
        {
          goods_name, goods_price, goods_number, goods_weight,
          goods_cat: goods_cat.join(','),
          goods_introduce,
          pics,
          radio
        });
      // console.log(res);
      if (res.data.meta.status === 201) {

        // 1. 弹出信息框
        this.$message({
          message: '添加商品成功',
          type: 'success',
          duration: 800
        })
         // 2. 跳转到商品列表
         this.$router.push('/goods');
      }
    }
  }
}



